<template>
  <div class="container register-wrap">
    <!-- <img class="bg" src="../../images/register/bg2.png" alt=""> -->
    <div class="wrap-center">
      <div class="title">个人账号</div>
      <el-form label-width="100px" class="dataForm">
        <el-form-item label="用户姓名">
          <el-input placeholder="请输入姓名"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-radio-group>
            <el-radio label="男"></el-radio>
            <el-radio label="女"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="证件类型">
          <el-input placeholder="请输入证件类型"></el-input>
        </el-form-item>
        <el-form-item label="证件号码">
          <el-input placeholder="请输入证件号码"></el-input>
        </el-form-item>
        <el-form-item label="证件有效期">
          <el-date-picker
            style="width: 260px"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-checkbox v-model="checked" fill="#B90909">长期</el-checkbox>
        </el-form-item>
        <el-form-item label="国籍">
          <el-input placeholder="请输入证件类型"></el-input>
        </el-form-item>
        <el-form-item label="职业">
          <el-input placeholder="请输入证件类型"></el-input>
        </el-form-item>
         <el-form-item label="住宅地址">
          <el-input placeholder="请输入住宅地址"></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input placeholder="请输入手机号"></el-input>
        </el-form-item>
        <el-form-item label="验证码">
          <el-input placeholder="请输入验证码"></el-input>
        </el-form-item>
        <el-form-item label="所属银行">
          <el-input placeholder="请输入所属银行"></el-input>
        </el-form-item>
         <el-form-item label="借记卡号">
          <el-input placeholder="请输入借记卡号"></el-input>
        </el-form-item>
      </el-form>
      <div class="btn-wrap">
        <button @click="$router.push({name: 'certification'})">上一步</button>
        <button class="common-btn">提交审核</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checked: false
    }
  },
  methods:{

  }
}
</script>


<style lang="less" scoped>
.register-wrap {
  position: relative;
  background: url(../../images/register/bg2.png) no-repeat;
  background-size: 100% 100%;
  padding: 30px 0;
  .wrap-center {
    width: 594px;
    margin: 0 auto;
    background: #fff;
    padding: 30px 58px 45px 58px;
    box-sizing: border-box;
    .title {
      color: #1A1A1A;
      font-size: 16px;
      font-weight: 500;
      text-align: center;
      margin-bottom: 32px;
    }
    .dataForm {
      display: block;
      margin: 0 auto 0 20px;
      /deep/ .el-input {
        width: 260px;
      }
      /deep/ .el-form-item__label{
        color: #1F1E1E;
        font-weight: 500;
      }
      /deep/.el-checkbox .is-checked .el-checkbox__inner{
        background-color: #B90909;
        border-color: #B90909;
      }
      /deep/.el-checkbox__input.is-checked+.el-checkbox__label {
        color: #B90909;
      }
    }

    .btn-wrap {
      display: flex;
      justify-content: space-around;
      button {
        width: 169px;
        height: 40px;
        font-size: 14px;
        font-weight: 500;
        &:first-child {
          background: #fff;
          border: 1px solid #BFBFBF;
          border-radius: 4px;
          color: #5E5E5E;
          font-size: 14px;
        }
      }
    }
  }
}
</style>


